.wrapperBlocksMaching {
  width: 70vw;
  /* height: 65vh; */
  /* background-color: rgb(228, 190, 190); */
  margin: 20px 0 0 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: stretch;
  flex-wrap: wrap;
  align-content: center;
  position: relative;
  /* flex-flow: column wrap; */
}

.englishLeftColumn {
  margin-right: 45px;
}
.chartsBlocksMaching {
  margin: 15px 0 15px 0;

  padding: 20px 40px;

  color: #434553;
  width: 15vw;
  height: 3vh;

  cursor: pointer;
  text-align: left;

  font-weight: 400;
  font-style: normal;
  font-size: 20px;
  background-color: #ffffffe7;
  border: 2px solid transparent;

  box-shadow: -5px -5px 15px #ffffff, 5px 5px 15px #e9ecf4;
  border-radius: 8px;
}

.chartsBlocksMaching:hover {
  box-shadow: -5px -5px 15px #ecebeb, 5px 5px 15px #c3c6ce;
}

.chartsBlocksMaching:nth-child(1),
.chartsBlocksMaching:nth-child(6) {
  margin: 0 0 0 0;
}

/* .inactive {
  border: 2px solid transparent;
} */

.activeBlocksMaching {
  border: 2px solid blue;
}
