.wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
  height: 100%;
  /* border: 1px solid #6e6e6e; */
}

.tableBox {
  width: 40%;
  height: 50%;
  /* border: 1px solid #6e6e6e; */
}

.wordCard {
  background-color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 70px;
  color: #434553;
  font-weight: 400;
  font-style: normal;
  font-size: 20px;
  border-radius: 12px;
  box-shadow: -5px -5px 15px #ffffff, 5px 5px 15px #e9ecf4;
  margin: 15px 0;
}

.wordCard:hover {
  box-shadow: -5px -5px 15px #ecebeb, 5px 5px 15px #c3c6ce;
}

